@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{
    margin-top: v.$separacion;
    width: 100%;
    font-weight:bold;
}


.campo{
    
    display:flex;
    margin-bottom:2rem;
    align-items: center;
    label{
        flex: 0 0 13rem;

    }
    input{
        font-family:'Times New Roman', Times, serif;
        flex: 1;
        border:none;
        padding: calc(v.$separacion / 4);
        border-radius: 5px;

    }
}